  body{
    font-family: 'Lato', sans-serif;
  }
  
  .wrapper{
    position: fixed;
    top: 8%;
    left: 40%;
    transform: translate(-50%, -50%);
  }

  .wrapper2{
    position: fixed;
    top: 8%;
    left: 60%;
    transform: translate(-50%, -50%);
  }
  
  .link_wrapper{
    position: relative;
  }
  
  a{
    display: block;
    width: 250px;
    height: 50px;
    line-height: 50px;
    font-weight: bold;
    text-decoration: none;
    background: white;
    text-align: center;
    color: rgb(0, 0, 0);
    text-transform: uppercase;
    letter-spacing: 1px;
    border: 3px solid white;
    transition: all .35s;
    border-radius: 20px;
  }
  
  .icon{
    width: 50px;
    height: 50px;
    border: 3px solid transparent;
    position: absolute;
    transform: rotate(45deg);
    right: 0;
    top: 0;
    z-index: -1;
    transition: all .35s;
  }
  
  .icon svg{
    width: 30px;
    position: absolute;
    top: calc(50% - 15px);
    left: calc(50% - 15px);
    transform: rotate(-45deg);
    fill: #cc952e;
    transition: all .35s;
  }
  
  a:hover{
    width: 200px;
    border: 3px solid #290F02;
    background: transparent;
    color: #290F02;
  }
  
  a:hover + .icon{
    border: 3px solid #290F02;
    right: -25%;
  }

  footer{
    position: absolute;
    margin-top: 300px;
}
footer a{
    color: #F5E53A;
}
footer img{
    width: 32px;
    height: 32px;
    vertical-align: middle;
}

